body
  font-family: 'Roboto'
.hide
  display: block !important
.txt
  font-weight: 300 !important
.preloader
  width: 100%
  height: 100%
  position: fixed 
  display: none
  z-index: 199
  background: white
  #preloader
    width: 400px
    height: 400px
    position: absolute
    top: 50%
    left: 50%
    transform: translateY(-50%) translateX(-50%)
    circle
      &.outer
        fill: transparent
        stroke: $blue
        stroke-dashoffset: 1000
        stroke-width: 20
        stroke-dasharray: 1000
        transform-origin: 50% 50%
        animation: spin 2s linear forwards infinite
      &.drop
        fill: $blue
        stroke: transparent
        transform-origin: 50% 50%
        animation: drop 2s linear forwards infinite

  @keyframes spin
    0%
      stroke-dashoffset: 1000
      transform: rotate(-90deg)
      stroke-width: 20

    25%
      stroke-dashoffset: 800
      stroke-width: 30

    80%
      stroke-dashoffset: 1000
      transform: rotate(270deg)
      stroke-width: 20


  @keyframes drop
    0%
      transform: translate(0, -150px)
      opacity: 1

    34%
      transform: translate(0, 150px)
      opacity: 1

    70%
      transform: translate(0, 0)
      opacity: 0

    100%
      opacity: 0

